import React from 'react';
// import './App.css';


class ReceptionOfficeTable extends React.Component{
    render(){
        return (
            <section id="about" className="about">
                <div className="container" data-aos="fade-up">

                    <div className="section-title" style={{marginTop: "90px"}}>
                        <h3>Live Clean Rwanda Inventories</h3>
                        <p>Live Clean Rwanda Inventories is only Viewed by anyone with Inventory QR Code Scanned, and this is done for security reasons. Once You get this Inventory, Please first confirm if it's no-longer our inventory</p><hr/>
                    </div>

                    <div className="row">
                    <div className="col-lg-3" data-aos="fade-right">
                        <img src="assets/img/about.jpg" className="img-fluid" alt="" />
                    </div>
                    <div className="col-lg-9 pt-4 pt-lg-0 content">
                        
                        <div className="alert alert-success" style={{padding: "5px", fontWeight: "bold", fontSize: "25px", textAlign: "center"}}><i style={{color: "orange"}}>STATUS:</i> AVAILABLE</div><hr/>
                        <h3>Reception Office Table</h3>
                        <table className="tabel table-bordered">
                            <tr>
                                <th>Decription:</th>
                                <td>The Office Table is still in Good Condition and it has 3 pull and push cupboards</td>
                            </tr> 
                            <tr>
                                <th>Quantity:</th>
                                <td>1</td>
                            </tr> 
                            <tr>
                                <th>Color:</th>
                                <td>Chocolate</td>
                            </tr> 
                        </table>
                    </div>
                    </div>

                </div><hr/>
            </section>
            // {/* <!-- End About Us Section --> */}
        );  
    }
}
export default ReceptionOfficeTable;