import React from "react";
import "../App.css";

class WhatsappShop extends React.Component {
  render() {
    return (
        <div>
            <div style={{backgroundColor: "#fff", height: "60px", width: "100%"}}>
                <div className="section-title" style={{marginTop: "130px"}}>
                    <h3>Book Online Via <span>WhatsApp Shop</span></h3>
                </div>
            </div>
            <div className="container">
                <div style={{boxShadow: "0px 0px 16px 2px rgba(0, 255, 166, 1)"}}>
                    <iframe src="https://bit.ly/3wO0bSm" name="WhatsApp Shop" scrolling="Yes" height="1000px" width="100%" style={{border: "none"}}></iframe>
                </div>
            </div>
           
        </div>
    );
  }
}
export default WhatsappShop;
