// Header
import Header from './layout/Header';
// Body Content
import Home from './layout/Home';
import About from './layout/About';
import Charity from './layout/Charity';
import Clients from './layout/Clients';
// import Blog from './layout/Blog';
// import BlogSingle from './layout/BlogSingle';
// import Abel from './layout/Abel';
import Pricing from './layout/Pricing';
// import FumigationPrice from './layout/calculate/FumigationPrice';
import Gallery from './layout/Gallery';
import OnlineAgent from './layout/OnlineAgent';
import Contact from './layout/Contact';
import Footer from './layout/Footer';
// Modal Services
import DeepCleaning from './modal/DeepCleaning';
import Fumigation from './modal/Fumigation';
import Laundry from './modal/Laundry';
import ShoeCare from './modal/ShoeCare';
import CarpetSofa from './modal/CarpetSofa';
import MoveInOut from './modal/MoveInOut';
import LawnGarden from './modal/LawnGarden';
import WaterTank from './modal/WaterTank';
// Customization in Package Pricing
import DeepCustomize from './layout/Customization/DeepCustomize';
//  STAFF DETAILS
import Noella from './layout/staff/Noella';
import Muhamudu from './layout/staff/Muhamudu';
import Joselyne from './layout/staff/Joselyne';
import JeanMarie from './layout/staff/JeanMarie';
import KalisaEddy from './layout/staff/KalisaEddy';
// App CSS
import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Micheal from './layout/staff/Micheal';
import Claude from './layout/staff/Claude';
import Angela from './layout/staff/Angela';
import KalisaDelphine from './layout/staff/KalisaDelphine';
import FumigationCustomize from './layout/Customization/FumigationCustomize';
import LaundryCustomize from './layout/Customization/LaundryCustomize';
import ShoeCareCustomize from './layout/Customization/ShoeCareCustomize';
import CarpetSofaCustomize from './layout/Customization/CarpetSofaCustomize';
import LawnGardenCustomize from './layout/Customization/LawnGardenCustomize';
import WaterTankCustomize from './layout/Customization/WaterTankCustomize';
// Inventory
import Inventory from './layout/Inventory/Inventory';
import Reception from './layout/Inventory/Reception';
import ManagerOffice from './layout/Inventory/ManagerOffice';
import ITRoom from './layout/Inventory/ITRoom';
import Stock from './layout/Inventory/Stock';
import Laundrys from './layout/Inventory/Laundrys';
import Kitchen from './layout/Inventory/Kitchen';
// Inventory Tools
import ReceptionOfficeTable from './layout/Inventory/Products//ReceptionTools/ReceptionOfficeTable';
import ReceptionOfficeChair from './layout/Inventory/Products/ReceptionTools/ReceptionOfficeChair';
import WhatsappShopBtn from './layout/WhatsappShopBtn';
import WhatsappShop from './layout/WhatsappShop';
// import CustomerSurvay from './layout/CustomerSurvey';

function App() {
  return (
    <Router>
      {/* Header */}
      <div>{<Header/>}</div>
      {/* End Header */}
      <main id="main">
        <Switch>
          {/* Body Content */}
          <Route path="/" exact component={Home} exact={true} />
          <Route path="/About" component={About} />
          <Route path="/Pricing" component={Pricing} />
          {/* <Route path="/Abel" component={Abel} /> */}
          <Route path="/Clients" component={Clients} />
          <Route path="/OnlineAgent" component={OnlineAgent} />
          {/* <Route path="/Blog" component={Blog} /> */}
          {/* <Route path="/BlogSingle" component={BlogSingle} /> */}
          <Route path="/Charity" component={Charity} />
          <Route path="/Gallery" component={Gallery} />
          {/* <Route path="/CustomerSurvay" component={CustomerSurvay} /> */}

          {/* SERVICE PRICE CUSTOMIZATION */}
          <Route path="/DeepCustomize" component={DeepCustomize} />
          <Route path="/FumigationCustomize" component={FumigationCustomize} />
          <Route path="/LaundryCustomize" component={LaundryCustomize} />
          <Route path="/ShoeCareCustomize" component={ShoeCareCustomize} />
          <Route path="/CarpetSofaCustomize" component={CarpetSofaCustomize} />
          <Route path="/LawnGardenCustomize" component={LawnGardenCustomize} />
          <Route path="/WaterTankCustomize" component={WaterTankCustomize} />

          {/* WHATSAPP SHOP */}
          <Route path="/WhatsappShop" component={WhatsappShop} />
          
          {/* STAFF DETAILS */}
          <Route path="/Noella" component={Noella} />
          <Route path="/Muhamudu" component={Muhamudu} />
          <Route path="/Joselyne" component={Joselyne} />
          <Route path="/JeanMarie" component={JeanMarie} />
          <Route path="/KalisaEddy" component={KalisaEddy} />
          <Route path="/KalisaDelphine" component={KalisaDelphine} />
          <Route path="/Micheal" component={Micheal} />
          <Route path="/Claude" component={Claude} />
          <Route path="/Angela" component={Angela} />

          {/* DEPARTENT INVENTORIES */}
          <Route path="/Inventory" component={Inventory} />
          <Route path="/Reception" component={Reception} />
          <Route path="/ManagerOffice" component={ManagerOffice} />
          <Route path="/ITRoom" component={ITRoom} />
          <Route path="/Stock" component={Stock} />
          <Route path="/Laundrys" component={Laundrys} />
          <Route path="/Kitchen" component={Kitchen} />

          {/* PRODUCTS (TOOLS) DETAILS */}
          <Route path="/ReceptionOfficeTable" component={ReceptionOfficeTable} />
          <Route path="/ReceptionOfficeChair" component={ReceptionOfficeChair} />
        </Switch>
        <div>{<WhatsappShopBtn />}</div>
        <div>{<Contact />}</div>
        {/* End Body Content */}
      </main>
      {/* Footer */}
      <footer id="footer">
        <div>{<Footer/>}</div>
      </footer>
      {/* End Footer */}

      {/* Modal Services */}
      <div>{<DeepCleaning/>}</div>
      <div>{<Fumigation/>}</div>
      <div>{<Laundry/>}</div>
      <div>{<ShoeCare/>}</div>
      <div>{<CarpetSofa/>}</div>
      <div>{<MoveInOut/>}</div>
      <div>{<LawnGarden/>}</div>
      <div>{<WaterTank/>}</div>
      {/* End Modal Services */}
    </Router>
  );
}

export default App;
