import React from 'react';
import '../App.css';

class Contact extends React.Component{
    render(){
        return (
            <section id="contact" className="contact">
                <div className="container">
                    <div className="section-title">
                        <h3>Find Our <span>Contacts</span></h3>
                    </div>

                    <div className="row">
                        <div className="col-lg-6">
                            <div className="info-box mb-4">
                                <i className="bx bx-map"></i>
                                <h3>Our Address</h3>
                                <p>KK 10 Ave, KK 504 St - Kicukiro, Kigali <br/> Next to Word Food Program (WFP)</p>
                            </div>
                        </div>

                        <div className="col-lg-3 col-md-6">
                            <div className="info-box  mb-4">
                                <i className="bx bx-envelope"></i>
                                <h3>Email Us</h3>
                                <p>info@livecleanrwanda.com <br/> eddy@livecleanrwanda.com</p>
                            </div>
                        </div>

                        <div className="col-lg-3 col-md-6">
                            <div className="info-box  mb-4">
                                <i className="bx bx-phone-call"></i>
                                <h3>Call Us</h3>
                                <p>+250 788 358 024 <br/> +250 788 803 564</p>
                            </div>
                        </div>

                    </div>

                    <div className="row">
                        <div className="col-lg-6 ">
                            <iframe className="mb-4 mb-lg-0" src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15949.820030104316!2d30.1018445!3d-1.9721644!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xb450952b1c4ce9e!2sLive%20Clean%20Rwanda%20LTD!5e0!3m2!1sen!2srw!4v1604736840007!5m2!1sen!2srw" frameborder="0" style={{border: "0", width: "100%", height: "384px"}} allowfullscreen></iframe>
                        </div>

                        <div className="col-lg-6">
                            <form role="form" method="post" id="reused_form" className="contact">
                                <div className="row">
                                    <div className="col-sm-6 form-group">
                                        <label for="name"> Your Name:</label>
                                        <input type="text" className="form-control" id="name" name="name" required/>
                                    </div>
                                    <div className="col-sm-6 form-group">
                                        <label for="email"> Email:</label>
                                        <input type="email" className="form-control" id="email" name="email" required/>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-12 form-group">
                                        <label for="message"> Message:</label>
                                        <textarea className="form-control" type="textarea" name="message" id="message" maxlength="6000" rows="7"></textarea>
                                    </div>
                                </div>
                                
                                <div className="row">
                                    <div className="col-sm-12 form-group php-email-form">
                                        <button type="submit" className="btn btn-default pull-right" >Send &rarr;</button>
                                    </div>
                                </div>
                            </form>

                            <div id="success_message" style={{width: "100%", height: "100%", display: "none"}}> <h3>Thank You!</h3> </div>
                            <div id="error_message" style={{width: "100%", height: "100%", display: "none"}}> <h3>Error</h3> Sorry there was an error sending your form. </div>
                        </div>
                    </div>
                </div>
            </section>
        );  
    }
}
export default Contact;